<template>
  <div class="outBox">
    <van-tabs v-model="active" swipeable @change="toggleOrderType" sticky title-active-color='#f80'>
      <van-tab :title="item.title" v-for="(item,index) in tabContent" :key="index" >
        <div class="order-content">
          <div class="order-items">
            <van-empty description="暂无订单数据，快去下单吧。" v-if="orderCode.length<=0" />
            <!-- 每一订单 start-->
            <div class="order-item" v-for="(item,index) in orderCode" :key="index" v-else>
              <div class="order-item-title">
                <div class="fl order-no">{{item}}</div>
                <div class="fr" v-if="everyoneOrderData[item].status == 2">
                  <span class="confirm fl afterConfirm">已收货</span>
                  <span class="delete fl" @click="delOrder(item)">
                    <van-icon class="delete-icon" name="delete"  />
                  </span>
                </div>
                <div class="fr" v-else>
                  <span class="confirm beforeConfirm" @click="confirmProduct(item)">确认收货</span>
                  
                </div>
              </div>

              <div class="items" v-for="(ite,ind) in everyoneOrderData[item].data" :key="ind">
                <div class="clearfix">
                  <div class="fl pro-img">
                    <img
                      class="auto-img"
                      :src="ite.smallImg"
                      alt
                    />
                  </div>
                  <div class="fl pro-info">
                    <div class="fl">
                      <div class="names">
                        <div class="zh-name">{{ite.name}}</div>
                        <div class="en-name">{{ite.enname}}</div>
                      </div>
                      <div class="rule">{{ite.rule}}</div>
                    </div>
                    <div class="fr">
                      <div class="price">￥{{ite.price}}</div>
                      <div class="count">x{{ite.count}}</div>
                    </div>
                  </div>
                </div>
              </div>

              <div class="order-total clearfix">
                <!-- <div class="fr clearfix">
                  <span class="fl total-count">共计 2 件商品 合计：</span>
                  <span class="fl total-price">￥3</span>
                </div> -->

                <div class="order-date fl">{{everyoneOrderData[item].createdAt | formatDate('yyyy-MM-DD hh:mm:ss')}}</div>
                <div class="fr clearfix">
                  <span class="fl total-count">共计 {{everyoneOrderData[item].count}} 件商品 合计：</span>
                  <span class="fl total-price">￥{{everyoneOrderData[item].total}}</span>
                </div>

              </div>
            </div>
            <!-- 每一订单 end-->
          </div>
        </div>
      </van-tab>
      <!-- <van-tab title="待收货">内容 2</van-tab>
      <van-tab title="已收货">内容 3</van-tab> -->
    </van-tabs>
    <tabbar></tabbar>
  </div>
</template>

<script>
import "../../assets/less/order.less";
import tabbar from "../../components/tabbar";
export default {
  data() {
    return {
      active: '0',
      isRecieve: false,
      orderCode:[],
      everyoneOrderData:{},
      tabContent: [{title: '全部'},{title: '待收货'},{title: '已收货'}],

    };
  },
  components: {
    tabbar
  },
  created() {
    this.findOrder();
    //判断
    if(!localStorage.getItem('__Tk')){
      localStorage.setItem('isloginCbg','')
    }
  },
  methods: {
    //切换订单类型
    toggleOrderType(){
      this.findOrder()

    },

    //查询订单接口
    findOrder() {
      let tokenString = localStorage.getItem("__Tk");

      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0
      });

      this.axios({
        method: "GET",
        url: "/findOrder",
        params: {
          appkey: this.appkey,
          tokenString,
          status: this.active
        }
      })
        .then(result => {
          this.$toast.clear();
          
          if (result.data.code == 70000) {
            //给订单时间排序
              result.data.result.sort((a,b) => {
                return new Date(b.createdAt).getTime()-new Date(a.createdAt).getTime()
              })

            let arr = []
            result.data.result.forEach(e => {
                
              if(arr.indexOf(e.oid)<0){
                arr.push(e.oid)
              }
            });
            
            this.orderCode = arr

            let obj = {}
            result.data.result.forEach(e => {
                
                if(!obj[e.oid]){
                  obj[e.oid] = {
                    data:[e],
                    status:e.status,
                    count:e.count,
                    total:e.count*e.price,
                    createdAt:e.createdAt
                  }
                }else{
                  obj[e.oid].count+=e.count;
                  obj[e.oid].total+=e.count*e.price;
                  obj[e.oid].data.push(e)
                }
                
            });
            
            this.everyoneOrderData = obj

          }
        })
        .catch(() => {
          this.$toast.clear();
          
        });
    },

    //确认收货
    confirmProduct(oid){
      
      
      let tokenString = localStorage.getItem("__Tk");

      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0
      });

      this.axios({
        method: "POST",
        url: "/receive",
        data: {
          appkey: this.appkey,
          tokenString,
          oid
        }
      })
        .then(result => {
          this.$toast.clear();
          
          if(result.data.code == 80000){
            
            this.everyoneOrderData[oid].status = 2;

            if(this.active == 1){
              //
              let index = this.orderCode.indexOf(oid)
              this.orderCode.splice(index,1)
            }
          }
        })
        .catch(() => {
          this.$toast.clear();
          
        });
    },

    //删除订单
    delOrder(oid){
      let tokenString = localStorage.getItem("__Tk");

      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0
      });

      this.axios({
        method: "POST",
        url: "/removeOrder",
        data: {
          appkey: this.appkey,
          tokenString,
          oid
        }
      })
        .then(result => {
          this.$toast.clear();
          
          if(result.data.code == 90000){
              let index = this.orderCode.indexOf(oid)
              this.orderCode.splice(index,1)
          }
        })
        .catch(() => {
          this.$toast.clear();
          
        });
    }
  }
};
</script>

<style lang="less" scoped>
</style>