<template>
  <div>
	  <!-- <router-view></router-view> -->
    <van-tabbar v-model="active" active-color='#f80'>
      <van-tabbar-item 
	  replace v-for="(item,index) in lists"
	  :key="index"
	  @click="goRouter(item.name)"
	  >
        <span>{{item.title}}</span>
        <template #icon>
          <i class="fa fa-lg" :class="item.icon" aria-hidden="true"></i>
        </template>
      </van-tabbar-item>
    </van-tabbar>
  </div>
</template>

<script>
export default {
	
  data() {
    return {
      active: 0,
      lists: [
          {
        	  title:'菜单',
			  icon:'fa-bars',
			  name:'menu',
			  url:'menu'
		  },
		  {
        	  title:'订单',
			  icon:'fa-indent',
			  name:'order',
			  url:'order'
		  },
		  {
        	  title:'购物车',
			  icon:'fa-shopping-cart',
			  name:'shopcart',
			  url:'shopcart'
		  },
		  {
        	  title:'我的',
			  icon:'fa-user',
			  name:'my',
			  url:'my'
          }
      ]
    }
  },
	methods:{
		goRouter(value){
			this.$router.push({name:value})
		},
		
	},
	created(){
		//防止刷新页面高亮显示不正确
		let str = location.hash
		
		
		for(let i =0;i<this.lists.length;i++){
			if(str.indexOf(this.lists[i].url)>0){
				this.active = i
			}
		}
	}
};
</script>

